import React, { Component } from 'react';

class Form extends Component{
		constructor(props){
			super(props);
			this.initialState={
				name:'',
				job:''
			}
			this.state = this.initialState;
		}
		
		handleChange = event => {
    		const {name, value} = event.target;
    		this.setState({
        		[name] : value
    		});
    	};
		handleSubmit = character => {
    		this.setState({characters: [...this.state.characters, character]});
		}
		submitForm = () => {
    			this.props.handleSubmit(this.state);
	    		this.setState(this.initialState);
	    		clearData ();
		}
		
	    render(){
	    	const { name, job } = this.state;
		    return (
		        <form handleSubmit={this.handleSubmit}>
		     		<div className="input-group mb-3">
	  					<div className="input-group-prepend">
	    					<span className="input-group-text" id="basic-addon1">Name</span>
	  					</div>
	  					<input type="text" name='name' class="form-control name" placeholder="Name" aria-label="Username" aria-describedby="basic-addon1"
	  					onChange={this.handleChange}	
	  					/>
					</div>
	            	
	            	<div className="input-group mb-3">
	  					<div className="input-group-prepend">
	    					<span className="input-group-text" id="basic-addon1">Job</span>
	  					</div>
	  					<input type="text" name='job' class="form-control job" placeholder="Job" aria-label="Job" aria-describedby="basic-addon1" 
						onChange={this.handleChange}
	  					/>
					</div>
					<input 
					    type="button" 
					    value="Submit"
					    className='btn-secondary'
						onClick={this.submitForm}
					    />
		        </form>
	    	);
		}	

};
 const clearData = ()=>{
			let name = document.querySelector('.name');
			let job = document.querySelector('.job');
			name.value = '';
			job.value = '';
		}

export default Form;